import React from 'react'
import { Card, Button } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'

const CountryCard = ({ country }) => {
  const { id, name, isoCode } = country
  const navigate = useNavigate()

  return (
    <Card key={id} style={{ width: '12rem', margin: '.5em' }}>
      <Card.Img variant='top' src={`https://flagcdn.com/${isoCode}.svg`} style={{ marginTop: '1em', border: '1px solid grey' }} />
      <Card.Body>
        <Card.Title>{name}</Card.Title>
        <Button
          variant='primary'
          onClick={() => { navigate(`/country/${isoCode}`) }}
        >
        </Button>
      </Card.Body>
    </Card>
  )
}

export default CountryCard
